*{
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding: 60px;
}

input {
  font-size: 1.2rem;
  width: 100%;
  color:grey;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}

.animate__animated.animate__bounce {
  --animate-duration: 6s;
}